import { Injectable } from '@angular/core';
import { CanActivate, ActivatedRouteSnapshot, RouterStateSnapshot } from '@angular/router';
import { Router } from '@angular/router';
import { Observable, BehaviorSubject } from 'rxjs';
@Injectable()
export class AuthGuard implements CanActivate {
    private role;
    private timeOut;
    private hours = 24;
    constructor(private router: Router) {
        this.role = localStorage.getItem('userRole');
        this.timeOut=localStorage.getItem('timeOut');
        console.log('user2', this.role);
        console.log('timeOut',this.timeOut)
        let now = new Date().getTime();
        console.log('now',now)
        if (now - this.timeOut > this.hours * 60 * 60 * 1000) {
            localStorage.clear()
            localStorage.setItem('timeOut',now.toString());
          }
    }

    canActivate(childRoute: ActivatedRouteSnapshot, state: RouterStateSnapshot): Observable<boolean> | Promise<boolean> | boolean {
        if(this.role=='"SUPERADMIN"'||this.role=='"ADMIN"'||this.role=='"COUNTER"'){
            return true;
        }
        this.router.navigate(['/login']);
        localStorage.clear();
        return false;
    }
    canActivateChild(childRoute: ActivatedRouteSnapshot, state: RouterStateSnapshot): Observable<boolean> | Promise<boolean> | boolean {
        if(this.role=='"COUNTER"'){
            this.router.navigate(['/login']);
            localStorage.clear();
            return false;
        }
        return true;
    }

}
